<template>
    <section class="loginContainer">
      <div class="loginInner">
        <div class="login_header">
          <h2 class="login_logo">硅谷外卖</h2>
          <div class="login_header_title">
            <a href="javascript:;" :class="{on : flag}" @click="change">短信登录</a>
            <a href="javascript:;" :class="{on : flag2}" @click="change">密码登录</a>
          </div>
        </div>
        <div class="login_content">
          <form>
            <div :class="{on : flag}">
              <section class="login_message">
                <input
                  type="tel" maxlength="11"
                  placeholder="手机号"
                  v-model="tele"
                >
                <button :disabled="!isPhone || time > 0"
                        class="get_verification"
                        :class="{right_phone_number : time <= 0 && isPhone}"
                        @click.prevent="sendCode"
                >{{time > 0 ? `已发送${time}s` : "获取验证码"}}</button>
              </section>
              <section class="login_verification">
                <input type="tel" maxlength="6" placeholder="验证码" v-model="code">
              </section>
              <section class="login_hint">
                温馨提示：未注册硅谷外卖帐号的手机号，登录时将自动注册，且代表已同意
                <a href="javascript:;">《用户服务协议》</a>
              </section>
            </div>
            <div :class="{on : flag2}">
              <section>
                <section class="login_message">
                  <input type="tel" maxlength="11" placeholder="手机/邮箱/用户名" v-model="name">
                </section>
                <section class="login_verification">
                  <input :type="t" maxlength="8" placeholder="密码" v-model="psw">
                  <div class="switch_button" :class="ST" @click="handleC">
                    <div class="switch_circle" :style="`transform: translateX(${num}px);`"></div>
                    <span class="switch_text">{{msg}}</span>
                  </div>
                </section>
                <section class="login_message">
                  <input type="text" maxlength="11" placeholder="验证码" v-model="captcha">
                  <img ref="cap" class="get_verification" src="http://localhost:5000/captcha" alt="captcha" @click="updateCaptcha">
                </section>
              </section>
            </div>
            <button class="login_submit" @click.prevent="loginIn" @keyup.enter="loginIn">登录</button>
          </form>
          <a href="javascript:;" class="about_us">关于我们</a>
        </div>
        <a href="javascript:" class="go_back" @click="$router.push('/profile')">
          <i class="iconfont icon-jiantou2" ></i>
        </a>
      </div>
    </section>

</template>

<script>
  import {reqSendCode,reqLoginSms,reqLoginPsw} from "./../../api/index.js"
  import {Toast,MessageBox} from "mint-ui"
    export default {
        name: "Login",
      data(){
          return{
            flag : true,
            flag2 : false,
            t : "password",
            ST : "off",
            msg : "...",
            num : 0,
            tele:"",//电话
            time:0,
            code :"",   //发送回来的验证码
            psw : "", // 密码
            name : "",  //用户名
            captcha : ""
          }
      },
      computed:{
          isPhone(){
            return /^1\d{10}$/.test(this.tele)
          }
      },
      methods:{
        change(){
          this.flag = !this.flag;
          this.flag2 = !this.flag2
        },
        handleC(){
          this.ST = (this.ST == "on" ? "off" : "on");
          this.t = (this.t == "tel" ? "password" : "tel");
          this.num = (this.num == 27 ? 0 : 27);
          this.msg = (this.msg == "abc" ? "..." : "abc");
        },
        async sendCode(){
          this.time = 30;
          const result = await reqSendCode(this.tele);
          if(result.code == 0){
            Toast("发送成功");
            var timer = setInterval(()=>{
              this.time--;
              if(this.time==0) {
                clearInterval(timer)
              }
            },1000)
          }else {
            MessageBox.alert("发送失败")
          }
        },
        updateCaptcha(){
          this.$refs.cap.src = `http://localhost:5000/captcha?time=${Date.now()}`
        },

        async loginIn(){
          const {psw,name,flag,flag2,captcha,tele,code,isPhone} = this;
          let result;
          if(flag){
            if(!isPhone){
              return MessageBox.alert("手机号不合法")
            }else if(!/^\d{6}$/.test(code)){
              return MessageBox.alert("验证码必须为6位")
            }
            result = await reqLoginSms(tele,code);
            if(result.code==1){
              this.time = 0;
            }
          }

          if(flag2){
            if(!name){
              return MessageBox.alert("必须输入用户名")
            }else if(!psw){
              return MessageBox.alert("必须输入密码")
            }else if(captcha.length !== 4){
              return MessageBox.alert("验证码必须4位")
            }
            result = await reqLoginPsw(name,psw,captcha);
            if(result.code !== 0){
              this.updateCaptcha();
            }
          }

          if(result.code == 0){
            this.$store.dispatch("saveUser",result.data);
            this.$router.replace("/profile")
          }else {
            MessageBox.alert(result.msg);
            this.updateCaptcha()
          }
        }
      }
    }
</script>

<style scoped lang="stylus">
    .loginContainer
      width 100%
      height 100%
      background #fff
      .loginInner
        padding-top 60px
        width 80%
        margin 0 auto
        .login_header
          .login_logo
            font-size 40px
            font-weight bold
            color #02a774
            text-align center
          .login_header_title
            padding-top 40px
            text-align center
            >a
              color #333
              font-size 14px
              padding-bottom 4px
              &:first-child
                margin-right 40px
              &.on
                color #02a774
                font-weight 700
                border-bottom 2px solid #02a774
        .login_content
          >form
            >div
              display none
              &.on
                display block
              input
                width 100%
                height 100%
                padding-left 10px
                box-sizing border-box
                border 1px solid #ddd
                border-radius 4px
                outline 0
                font 400 14px Arial
                &:focus
                  border 1px solid #02a774
              .login_message
                position relative
                margin-top 16px
                height 48px
                font-size 14px
                background #fff
                .get_verification
                  position absolute
                  top 50%
                  right 10px
                  transform translateY(-50%)
                  border 0
                  color #ccc
                  font-size 14px
                  background transparent
                  &.right_phone_number
                    color black
              .login_verification
                position relative
                margin-top 16px
                height 48px
                font-size 14px
                background #fff
                .switch_button
                  font-size 12px
                  border 1px solid #ddd
                  border-radius 8px
                  transition background-color .3s,border-color .3s
                  padding 0 6px
                  width 30px
                  height 16px
                  line-height 16px
                  color #fff
                  position absolute
                  top 50%
                  right 10px
                  transform translateY(-50%)
                  &.off
                    background #fff
                    .switch_text
                      float right
                      color #ddd
                  &.on
                    background #02a774
                  >.switch_circle
                    //transform translateX(27px)
                    position absolute
                    top -1px
                    left -1px
                    width 16px
                    height 16px
                    border 1px solid #ddd
                    border-radius 50%
                    background #fff
                    box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                    transition transform .3s
              .login_hint
                margin-top 12px
                color #999
                font-size 14px
                line-height 20px
                >a
                  color #02a774
            .login_submit
              display block
              width 100%
              height 42px
              margin-top 30px
              border-radius 4px
              background #4cd96f
              color #fff
              text-align center
              font-size 16px
              line-height 42px
              border 0
          .about_us
            display block
            font-size 12px
            margin-top 20px
            text-align center
            color #999
        .go_back
          position absolute
          top 5px
          left 5px
          width 30px
          height 30px
          >.iconfont
            font-size 20px
            color #999

</style>



